@import './variable.less'; // 变量

// 小图标 -----------------------------------------------------------------------------------------------------
.ico_tab_home_cur{.bg_img('../assets/img/tab_home_sel@2x.png')} /* 主页 */
.ico_tab_home{.bg_img('../assets/img/tab_home_sel@2x_65.png')}
.ico_tab_my_cur{.bg_img('../assets/img/tab_my_nor@2x_22.png')} /* 我 */
.ico_tab_my{.bg_img('../assets/img/tab_my_nor@2x.png')}
.ico_setting{.bg_img('../assets/img/-e-icon_setting@2x.png')} /* 设置 */
.ico_integral{.bg_img('../assets/img/-e-icon_integral@2x.png')} /* 积分 */
.ico_case{.bg_img('../assets/img/-e-icon_case@2x.png')} /* 病历 */
.ico_coupon_small{.bg_img('../assets/img/-e-icon_coupon_small@2x.png')} /* 优惠券 */
.ico_data{.bg_img('../assets/img/-e-icon_data@2x.png')} /* 记录 */
.ico_more{.bg_img('../assets/img/icon_more@2x_20.png')} /* 更多 */
.ico_edit{.bg_img('../assets/img/-e-nav_icon_edit@2x.png')} /* 编辑 */


// 公用类 -----------------------------------------------------------------------------------------------------

.no_content_bg_color{ // 添加背景色
  background-color: @no_content_bg_color;
}

.item_more { // 显示更多 cell
  padding: 0 0.3rem;
  height: 0.9rem;
  line-height: 0.9rem;
  background: #fff;
  .fz(28px);
  position: relative;
  text-indent: 0.12rem;
  &.new_msg:before{
    position: absolute;
    right:0.9rem;
    margin-top: 0.35rem;
    content:'';
    width: 0.2rem;
    height: 0.2rem;
    background: #ff6a4e;
    display: inline-block;
    border-radius: 50%;
  }
  .next_ico{
    position: absolute;
    right:0.44rem;
    margin-top: 0.25rem;
    position: absolute;
    display: inline-block;
    width: 0.4rem;
    height: 0.4rem;
    background-image: url(../assets/img/icon_more@2x_20.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }
  &:active{
    background: @active_color;
  }
  >.item_ico{
    content: '';
    display: inline-block;
    width: 0.5rem;
    height: 0.9rem;
    float: left;
    // background-image: url(../assets/img/-e-icon_data@2x.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }
}

.active_bg_color:active{ // 激活状态颜色
  background-color:@active_color;
}

.top_fixed{ // 让页面成为最顶层
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: scroll;
}

.-e-login_bg{ // 蓝色图片大背景
  background-image: linear-gradient(to bottom, #4577f0, #6992f6);
  .top_fixed;
  &::after{
    .top_fixed;
    content:'';
    background-image: url(../assets/img/-e-login_bg.png);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: 100%;
    z-index: -2;
  }
}

.-e-login_bg_and_null{ // 半屏背景
  &:before{
    content:'';
    display: block;
    position: fixed;
    top: 2.4rem;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: @no_content_bg_color;
  }
}

.content_box.pos_top{ // 让元素上浮
  position: relative;
  background: @no_content_bg_color;
  margin-top: 2.4rem;
  padding-top: 1.23rem;
  padding-bottom: 1.3rem;
  .float_top_box{

    position: absolute;
    top: -2.06rem;
    left: 0;
    bottom: 0;
    right: 0;

  }
}



