body {
  font-family: "PingFangSC", "PingFangSC-Medium", "sans-serif", "San Francisco", "Source Han Sans", "苹方", "Roboto", "思源黑体", "Microsoft Yahei", "微软雅黑";
  background: #f6f6f6;
  font-size: 14px !important;
  color: #253e67;
}
[data-dpr="2"] body {
  font-size: 28px !important;
}
[data-dpr="2.5"] body {
  font-size: 35px !important;
}
[data-dpr="2.75"] body {
  font-size: 39px !important;
}
[data-dpr="3"] body {
  font-size: 42px !important;
}
[data-dpr="4"] body {
  font-size: 56px !important;
}
html,
body,
ul,
li,
ol,
dl,
dd,
dt,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
fieldset,
legend,
img {
  margin: 0;
  padding: 0;
}
fieldset,
img {
  border: 0;
}
img {
  display: block;
}
address,
caption,
cite,
code,
dfn,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul,
ol {
  list-style: none;
}
input {
  padding-top: 0;
  padding-bottom: 0;
}
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
select,
input {
  vertical-align: middle;
}
select,
input,
textarea {
  font-size: 12px;
  margin: 0;
}
input[type="text"],
input[type="password"],
textarea {
  outline-style: none;
  -webkit-appearance: none;
}
textarea {
  resize: none;
}
table {
  border-collapse: collapse;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #cacaca;
}
input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #cacaca;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #cacaca;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #cacaca;
}
a,
a:hover {
  text-decoration: none;
}
.ico_tab_home_cur {
  background-image: url('../assets/img/tab_home_sel@2x.png');
}
/* 主页 */
.ico_tab_home {
  background-image: url('../assets/img/tab_home_sel@2x_65.png');
}
.ico_tab_my_cur {
  background-image: url('../assets/img/tab_my_nor@2x_22.png');
}
/* 我 */
.ico_tab_my {
  background-image: url('../assets/img/tab_my_nor@2x.png');
}
.ico_setting {
  background-image: url('../assets/img/-e-icon_setting@2x.png');
}
/* 设置 */
.ico_integral {
  background-image: url('../assets/img/-e-icon_integral@2x.png');
}
/* 积分 */
.ico_case {
  background-image: url('../assets/img/-e-icon_case@2x.png');
}
/* 病历 */
.ico_coupon_small {
  background-image: url('../assets/img/-e-icon_coupon_small@2x.png');
}
/* 优惠券 */
.ico_data {
  background-image: url('../assets/img/-e-icon_data@2x.png');
}
/* 记录 */
.ico_more {
  background-image: url('../assets/img/icon_more@2x_20.png');
}
/* 更多 */
.ico_edit {
  background-image: url('../assets/img/-e-nav_icon_edit@2x.png');
}
/* 编辑 */
.no_content_bg_color {
  background-color: #f6f6f6;
}
.item_more {
  padding: 0 0.3rem;
  height: 0.9rem;
  line-height: 0.9rem;
  background: #fff;
  font-size: 14px;
  position: relative;
  text-indent: 0.12rem;
}
[data-dpr="2"] .item_more {
  font-size: 28px;
}
[data-dpr="2.5"] .item_more {
  font-size: 35px;
}
[data-dpr="2.75"] .item_more {
  font-size: 39px;
}
[data-dpr="3"] .item_more {
  font-size: 42px;
}
[data-dpr="4"] .item_more {
  font-size: 56px;
}
.item_more.new_msg:before {
  position: absolute;
  right: 0.9rem;
  margin-top: 0.35rem;
  content: '';
  width: 0.2rem;
  height: 0.2rem;
  background: #ff6a4e;
  display: inline-block;
  border-radius: 50%;
}
.item_more .next_ico {
  right: 0.44rem;
  margin-top: 0.25rem;
  position: absolute;
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  background-image: url(../assets/img/icon_more@2x_20.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}
.item_more:active {
  background: #ececec;
}
.item_more > .item_ico {
  content: '';
  display: inline-block;
  width: 0.5rem;
  height: 0.9rem;
  float: left;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}
.active_bg_color:active {
  background-color: #ececec;
}
.top_fixed {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: scroll;
}
.-e-login_bg {
  background-image: linear-gradient(to bottom, #4577f0, #6992f6);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: scroll;
}
.-e-login_bg::after {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: scroll;
  content: '';
  background-image: url(../assets/img/-e-login_bg.png);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100%;
  z-index: -2;
}
.-e-login_bg_and_null:before {
  content: '';
  display: block;
  position: fixed;
  top: 2.4rem;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #f6f6f6;
}
.content_box.pos_top {
  position: relative;
  background: #f6f6f6;
  margin-top: 2.4rem;
  padding-top: 1.23rem;
  padding-bottom: 1.3rem;
}
.content_box.pos_top .float_top_box {
  position: absolute;
  top: -2.06rem;
  left: 0;
  bottom: 0;
  right: 0;
}



