
.main {
  height: 100%;
}
.main_loading {
  color: #ccc;
  text-align: center;
  width: 50%;
  margin: auto;
  position: absolute;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}



